.feedPreview {
  position: relative;
  min-height: 460px;
  background-image: url("../../../images/bigBackground.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding: 0 84px;

  &__card {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    .fullFeedCard {
      width: 476px;
      height: 340px;
      position: absolute;
      top: 60px;
      background: rgba(0, 0, 0, 0.5);

      .content {
        &__heading {
          font-size: 30px;
        }
      }
    }
  }
}