.mediaPreview {
  width: 100%;
  z-index: 999;
  background-image: url("../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-size: cover;
  border-top: 4px solid transparent;
  border-image: linear-gradient(90deg, #FA6E03, #FAB803);
  border-image-slice: 1;

  padding-top: 50px;
  padding-bottom: 60px;

  .info {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    &__heading {
      margin: 0;
      font-size: 36px;
      font-family: "PT Root UI Bold";
      color: white;
      margin-bottom: 21px;

      &__heading {
        position: relative;
        color: #FFFFFF;
        margin: 0px;
        margin-bottom: 12px;
        font-size: 24px;
        font-family: "PT Root UI Medium";
        font-weight: 400;
        padding-bottom: 12px;
        margin-bottom: 24px;

        &_orange {
          color: #FA6E03;
          border-bottom: 2px solid #FA6E03;
        }

        &_white {
          color: #FFFFFF;
          border-bottom: 2px solid #FFFFFF;

          &:after {
            content: '';
            height: 2px;
            width: 50%;
            background-color: #FFFFFF;
            position: absolute;
            bottom: -2px;
            left: -24px;
          }
        }
      }
    }

    .content {
      .mediaList {
        display: grid;
        grid-row-gap: 16px;
        grid-column-gap: 24px;
        grid-template-rows: 50px 200px 200px;
        grid-template-columns: 624px 300px 300px;
        grid-template-areas:  "nav1 nav1 ."
                              "photoCard1 photoCard2 ad1"
                              "photoCard1 photoCard3 ad1";

        .navigation {
          border-bottom: 2px solid #FFFFFF;
          display: flex;
          align-items: center;

          &__item {
            font-size: 24px;
            font-family: "PT Root UI Medium";
            color: white;

            &:first-of-type {
              position: relative;
              color: #FA6E03;
              margin-right: 25px;

              &:after {
                content: '';
                height: 2px;
                width: 100%;
                background-color:#FA6E03;
                position: absolute;
                bottom: -11.5px;
                left: 0;
              }
            }

            &:last-of-type {
              margin-left: auto;

              font-size: 16px;
              color: #FA6E03;
              font-family: "PT Root UI Medium";

              display: flex;
              align-items: center;

              svg {
                margin-left: 12px;
              }
            }
          }

          &:nth-of-type(1) {
            grid-area: nav1;
          }
        }

        .advertisement {
          &:nth-of-type(2) {
            grid-area: ad1;
          }
        }

        .photoFeedCard {
          &:nth-of-type(3) {
            grid-area: photoCard1;
          }
          &:nth-of-type(4) {
            grid-area: photoCard2;
          }
          &:nth-of-type(5) {
            grid-area: photoCard3;
          }
        }
      }
    }
  }
}