.mainFooter {
  border-top: 4px solid transparent;
  border-image: linear-gradient(90deg, #FA6E03 , #FAB803);
  border-image-slice: 1;
  background-color: #282C3B;

  .content {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;

    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      padding: 60px 84px;
    }

    &__ads {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    .ads {
      max-width: 282px;
      &__heading {
        text-transform: uppercase;
        font-family: "PT Root UI Medium";
        font-size: 13px;
        color: #9B9FAD;
        margin-bottom: 14px;
      }

      &__item {
        font-size: 16px;
        font-family: "PT Root UI Medium";
        color: white;
        margin-bottom: 8px;
        text-align: left;

        &_highlighted {
          color: #FA6E03;
        }
      }
    }

    &__contact {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    .contact {
      &__heading {
        text-transform: uppercase;
        font-family: "PT Root UI Medium";
        font-size: 13px;
        color: #9B9FAD;
        margin-bottom: 14px;
      }

      &__item {
        font-family: "PT Root UI Medium";
        font-size: 20px;
        color: #FFFFFF;
        margin-bottom: 15px;
      }
    }

    &__social {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
    }

    .social {
      &__info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
      }

      .info {
        &__heading {
          text-transform: uppercase;
          font-family: "PT Root UI Medium";
          font-size: 13px;
          color: #9B9FAD;
          margin-bottom: 14px;
        }

        .links {
          &__item {
            margin-right: 16px;
          }
        }
      }

      &__copyright {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }

      .copyright {
        &__logo {
          margin-bottom: 8px;
        }

        &__text {
          font-size: 16px;
          font-family: "PT Root UI Regular";
          color: #FFFFFF;
        }
      }
    }
  }
}