.socialMediaExpandedList {
  position: absolute;
  right: 0;
  overflow: hidden;

  display: flex;
  align-items: center;
  padding: 0 4px;

  background-color: #3E424F;
  border-radius: 5px;
  height: 44px;

  -webkit-transition: width .4s;
  transition: max-width .4s, min-width .4s;
  width: auto;

  &_expanded {
    max-width: 350px;
    min-width: 300px;
  }

  &_collapsed {
    max-width: 140px;
    min-width: 140px;
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 0 8px;
    color: white;

    &_expanding {
      animation-name: opacityPop;
      animation-duration: .75s;
    }
  }

  &__rest {
    transition: all .4s;

    &_expanded {
      transition: all .4s;
      transform: rotate(90deg);
    }
  }
}

@keyframes opacityPop {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}