.feedCard {
  position: relative;
  border-radius: 8px;

  &__content {
    display: flex;
    flex-direction: column;
  }

  .content {
    &__heading {
      margin: 0px;
    }

    &__category {
      position: relative;
      color: #FA6E03;

      &:after {
        content: '';
        height: 14px;
        width: 2px;
        background-color: #FA6E03;
        position: absolute;
        top: calc(50% - 7px);
        left: -16px;
      }
    }
  }
}

// Photo Card
.photoFeedCard {
  border-radius: 8px;
  overflow: hidden;
  max-height: 498px;
  background-color: #FFFFFF;

  .mediaAmount {
    position: absolute;
    top: 4px;
    left: 4px;
  }

  // sizes
  &_big {
    .content {
      &__heading {
        font-family: "PT Root UI Bold";
        font-size: 24px;
      }
    }
  }

  &_small {
    .content {
      &__heading {
        font-weight: 400;
        font-family: "PT Root UI Medium";
        font-size: 16px;
      }
    }
  }

  &__content {
    flex-grow: 0;
    color: white;
    position: absolute;
    bottom: 16px;
  }

  .content {
    &__heading {
      margin: 10px 0px;
    }
  }

  @media screen and (min-width: 768px) {
    &__content {
      padding: 0 36px;
    }
  }

  @media screen and (max-width: 767px) {
    &__content {
      padding: 0 16px 16px;
    }

    .content {
      &__heading {
        font-size: 20px;
        font-family: "PT Root UI Bold";
      }

      .feedInfo {
        svg path {
          fill: black;
        }

        .item {
          &__text {
            color: black;
          }
        }
      }
    }
  }
}

// Slider Card
.sliderFeedCard {
  height: 352px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 1)), url("../../../images/bigBackground.png") no-repeat;
  background-size: cover;

  .fullFeedCard {
    &__content {
      .content {
        &__heading {
          font-size: 24px;
        }
      }
    }
  }
}

// Half Card
.halfFeedCard {
  border-radius: 8px;
  overflow: hidden;
  background-color: #FFFFFF;

  .feedInfo {
    position: absolute;
    left: 16px;
    right: 16px;
    bottom: 16px;

    display: flex;
    color: #9B9FAD;
    font-size: 13px;
    justify-content: space-between;

    svg path {
      fill: #9B9FAD;
    }
  }

  // start of sizes
  &_small {
    .halfFeedCard__image {
      height: 170px;
    }
  }

  &_medium {
    .halfFeedCard__image {
      height: 260px;
    }
  }

  &_big {
    .halfFeedCard__image {
      height: 350px;
    }
  }

  // end of sizes ^^

  &__image {
    margin-bottom: 10px;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &__content {
    flex-grow: 0;
    color: black;
    padding: 0 16px 16px;
  }

  .content {
    &__heading {
      margin: 10px 0;
      font-size: 20px;
      font-family: "PT Root UI Medium";
      font-weight: 400;
    }
  }

  @media screen and (min-width: 768px) {
    &__image {
      height: 100%;
      width: 100%;
    }
  }

  @media screen and (max-width: 767px) {
    &__image {
      height: 204px;
      margin-bottom: 6px;
    }

    &__content {
      color: black;
      padding: 0 16px 16px;
    }

    .content {
      &__heading {
        font-size: 20px;
        font-family: "PT Root UI Bold";
      }

      .feedInfo {
        svg path {
          fill: black;
        }

        .item {
          &__text {
            color: black;
          }
        }
      }
    }
  }
}

// Full Regular Card
.fullRegularCard {
  .content {
    &__heading {
      @media screen and (min-width: 768px) {
        font-size: 20px;
        font-weight: 400;
        font-family: "PT Root UI Medium";
      }
    }
  }
}

// Full Card
.fullFeedCard {
  border-radius: 8px;
  overflow: hidden;
  max-height: 498px;
  background-color: #FFFFFF;

  &__image img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__content {
    flex-grow: 0;
    color: white;
  }

  .content {
    &__heading {
      margin: 10px 0px;
    }
  }

  @media screen and (min-width: 768px) {
    &__image {
      height: 100%;
      width: 100%;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1));
      }
    }
    &__content {
      position: absolute;
      padding: 0 36px;
      bottom: 36px;
    }
  }

  @media screen and (max-width: 767px) {
    &__image {
      height: 204px;
      margin-bottom: 6px;
    }

    &__content {
      color: black;
      padding: 0 16px 16px;
    }

    .content {
      &__heading {
        font-size: 20px;
        font-family: "PT Root UI Bold";
      }

      .feedInfo {
        svg path {
          fill: black;
        }

        .item {
          &__text {
            color: black;
          }
        }
      }
    }
  }
}