.feedInfo {
  display: flex;

  &__section {
    display: flex;
    align-items: center;
    .item {
      display: flex;
      align-items: center;
      &:first-of-type {
        margin-right: 20px;
      }

      &__text {
        &_gray {
          color: #9B9FAD;
        }
        &_white {
          color: #FFFFFF;
        }
      }

      &__logo {
        margin-right: 6px;
        display: flex;
        align-items: center;
        z-index: 999;
        &_white {
          svg path {
            fill: #FFFFFF;
          }
        }
        &_gray {
          svg path {
            fill: #9B9FAD;
          }
        }
      }
    }
  }
}