.popular {
  position: relative;
  border-top: 2px solid #282C3B;
  padding-top: 37px;
  margin-top: 40px;

  &__heading {
    position: absolute;
    top: -18px;
    padding-right: 13px;
    background-color: #F0F2F4;
    font-size: 24px;
    font-family: "PT Root UI Bold";
    overflow: hidden;
    color: #282C3B;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}