.specProjects {
  min-height: 100%;
  flex-grow: 1;

  &_centered {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }
  }

  &__preview {
    margin-bottom: 48px;
  }

  &__content {
    margin-top: 36px;
    display: grid;
    @media screen and (min-width: 1440px) {
      grid-row-gap: 24px;
      grid-column-gap: 24px;
      grid-template-rows: 200px 274px auto;
      grid-template-columns: repeat(4, 1fr);
      padding: 0 84px 48px;
    }

    .fullFeedCard {
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 3;
    }

    .advertisement:nth-child(1) {
      grid-column-start: 4;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 2;
    }

    .advertisement:nth-child(2) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 5;
      grid-row-end: 6;
    }

    .advertisement:nth-child(3) {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 5;
      grid-row-end: 6;
    }

    .advertisement:nth-child(4) {
      grid-column-start: 4;
      grid-column-end: 5;
      grid-row-start: 6;
      grid-row-end: 7;
      margin-top: 40px;
    }

    .newsline {
      max-height: 1272px;
      grid-column-start: 4;
      grid-column-end: 5;
      grid-row-start: 2;
      grid-row-end: 6;
    }

    .popular {
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 6;
      grid-row-end: 7;
    }
  }

  &__additionalContent {
    display: flex;
    align-items: flex-start;

    @media screen and (min-width: 1440px) {
      padding: 0 84px;
      margin-bottom: 48px;
    }

    .aside {
      @media screen and (min-width: 1440px) {
        width: 300px;
        margin-right: 24px;
      }

      .newsline {
        grid-area: newsLine;
        max-height: 656px;
        margin-bottom: 24px;
      }

      .advertisement {
        &:not(:last-of-type) {
          margin-bottom: 24px;
        }

        &:last-of-type {
          height: 466px;
        }
      }
    }

    .newsFeed {
      display: grid;
      grid-template-columns: 300px 138px 462px;
      grid-column-gap: 24px;
      grid-row-gap: 24px;
      grid-template-areas:  "ad1 ad2 ad2"
                            "halfCard1 halfCard1 halfCard2"
                            "fullCard1 fullCard1 fullCard1"
                            "halfCard3 halfCard3 halfCard4";

      .advertisement {
        &:nth-of-type(1) {
          grid-area: ad1;
        }

        &:nth-of-type(2) {
          grid-area: ad2;
        }
      }

      .fullFeedCard {
        height: 498px;
        grid-area: fullCard1;
      }

      .halfFeedCard {
        &_medium {
          width: 462px;
          min-height: 412px;

          &:nth-of-type(3) {
            grid-area: halfCard1;
          }

          &:nth-of-type(4) {
            grid-area: halfCard2;
          }

          &:nth-of-type(5) {
            grid-area: halfCard3;
          }

          &:nth-of-type(6) {
            grid-area: halfCard4;
          }
        }
      }
    }
  }

  &__feedPreview {
    margin-bottom: 48px;
  }

  &__economicsContent {
    margin-bottom: 48px;
  }

  &__media {
    margin-bottom: 48px;
  }

  &__ads {
    margin-bottom: 48px;

    display: grid;
    grid-template-columns: 624px 300px 300px;
    grid-template-rows: 200px;
    grid-column-gap: 24px;
    grid-template-areas: "ad1 ad2 ad3";

    .advertisement {
      &:nth-of-type(1) {
        grid-area: ad1;
      }
      &:nth-of-type(1) {
        grid-area: ad2;
      }
      &:nth-of-type(1) {
        grid-area: ad3;
      }
    }
  }
}