@import "~react-image-gallery/styles/scss/image-gallery.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'PT Root UI Bold';
    src: url("./fonts/PT-Root-UI_Bold.ttf");
}

@font-face {
    font-family: 'PT Root UI Medium';
    src: url("./fonts/PT-Root-UI_Medium.ttf");
}

@font-face {
    font-family: 'PT Root UI Light';
    src: url("./fonts/PT-Root-UI_Light.ttf");
}

@font-face {
    font-family: 'PT Root UI Regular';
    src: url("./fonts/PT-Root-UI_Regular.ttf");
}