.mainHeader {
  position: fixed;
  z-index: 1000;
  width: 100%;

  &__wrapper {
    background-color: #282C3B;
    height: 68px;
  }

  .wrapper {
    &__navigation {
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 84px;
      margin: 0 auto;
    }

    .navigation {
      &__logo {
        display: flex;
        align-items: center;

        @media screen and (min-width: 1440px) {
          margin-right: 48px;
        }
      }

      &__links {

      }

      &__rest {
        display: flex;
      }

      .rest {
        display: flex;
        flex-grow: 1;
        justify-content: flex-end;
        height: 100%;

        &__socialMedia {
          display: flex;
          align-items: center;
          position: relative;
          min-width: 150px;
          margin-right: 24px;
        }

        &__buttons {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 110px;
        }

        &__weather {
          margin-right: 23px;
        }
      }

      .links {
        &__item {
          color: white;
          text-decoration: none;
          position: relative;

          &_active {
            color: #FA6E03;

            &:after {
              content: '';
              height: 2px;
              width: 24px;
              border-top-right-radius: 2px;
              border-top-left-radius: 2px;
              background-color: #FA6E03;
              position: absolute;
              bottom: -23px;left: 50%;
              -webkit-transform: translateX(-50%);
              transform: translateX(-50%)
            }
          }

          @media screen and (min-width: 1440px) {
            margin-right: 23px;
            font-size: 18px;
            font-family: "PT Root UI Regular";
          }
        }
      }
    }
  }

  &__sections {
    background-color: #FFFFFF;
    border-bottom: 1px solid #E0E1E4;
    height: 46px;
  }

  .sections {
    &__list {
      position: relative;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 84px;
      margin: 0 auto;
    }

    .list {
      display: flex;

      &__arrow {
        box-shadow: 0px 0px 6px 10px #FFFFFF;
        background-color: white;
        z-index: 999;
      }

      &__leftIcon {
        position: absolute;
        left: 0;
      }

      &__rightIcon {
        position: absolute;
        right: 0;
      }
    }
  }

  &__notification {
    background-color: #FFFFFF;
    border-bottom: 1px solid #F0F2F4;
    height: 46px;
  }

  .notification {
    display: flex;
    align-items: center;
    padding: 0 84px;

    .content {
      display: flex;
      align-items: center;

      &__logo {
        display: flex;
        align-items: center;
        margin-right: 12px;
      }

      &__text {
        color: #FA6E03;
        font-size: 18px;
        font-family: "PT Root UI Medium";
      }
    }
  }
}