.specProjectsPreview {
  width: 100%;
  z-index: 999;
  background-image: url("../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-size: cover;
  border-top: 4px solid transparent;
  border-image: linear-gradient(90deg, #FA6E03 , #FAB803);
  border-image-slice: 1;

  .info {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    &__heading {
      font-size: 36px;
      font-family: "PT Root UI Bold";
      color: white;
      margin-top: 50px;
      margin-bottom: 21px;
    }

    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto;
      grid-column-gap: 24px;
      padding-bottom: 60px;

      .item {
        &__heading {
          position: relative;
          color: #FFFFFF;
          margin: 0px;
          margin-bottom: 12px;
          font-size: 24px;
          font-family: "PT Root UI Medium";
          font-weight: 400;
          padding-bottom: 12px;
          margin-bottom: 24px;

          &_orange {
            color: #FA6E03;
            border-bottom: 2px solid #FA6E03;
          }

          &_white {
            color: #FFFFFF;
            border-bottom: 2px solid #FFFFFF;
            &:after {
              content: '';
              height: 2px;
              width: 50%;
              background-color: #FFFFFF;
              position: absolute;
              bottom: -2px;
              left: -24px;
            }
          }
        }
      }
    }
  }
}