.popularCard {
  display: flex;
  flex-direction: column;
  width: 172px;

  &__image {
    width: 172px;
    height: 172px;
    border-radius: 100%;
    background-image: url("../../../../images/popularCard.png");
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 8px;
  }

  &__text {
    font-size: 15px;
    font-family: "PT Root UI Medium";
    color: #282C3B;
    line-height: 1.35;
  }
}