.economics {
  position: relative;
  border-top: 2px solid #282C3B;
  padding-top: 37px;

  &__heading {
    margin: 0;

    position: absolute;
    top: -18px;
    padding-right: 13px;
    background-color: #F0F2F4;
    font-size: 24px;
    font-family: "PT Root UI Bold";
    overflow: hidden;
    color: #282C3B;
  }

  &__cardsList {
    display: grid;
    grid-row-gap: 24px;
    grid-column-gap: 24px;

    grid-template-columns: 408px 192px 192px 84px 300px;
    grid-template-rows: 286px  478px 498px 478px 200px;
    grid-template-areas:  "fullRegular1 fullRegular2 fullRegular2 fullRegular3 fullRegular3"
                          "halfFeed1 halfFeed1 halfFeed2 halfFeed2 halfFeed2"
                          "fullFeed1 fullFeed1 fullFeed1 fullFeed1 ad1"
                          "halfFeed3 halfFeed3 halfFeed4 halfFeed4 halfFeed4"
                          "ad2 ad3 ad4 ad4 ad4";

    .advertisement {
      &:nth-of-type(1) {
        grid-area: ad1;
      }
      &:nth-of-type(2) {
        grid-area: ad2;
      }
      &:nth-of-type(3) {
        grid-area: ad3;
      }
      &:nth-of-type(4) {
        grid-area: ad4;
      }
    }

    .fullRegularCard {
      &:nth-of-type(5) {
        grid-area: fullRegular1;
      }
      &:nth-of-type(6) {
        grid-area: fullRegular2;
      }
      &:nth-of-type(7) {
        grid-area: fullRegular3;
      }
    }

    .halfFeedCard {
      &:nth-of-type(8) {
        grid-area: halfFeed1;
      }
      &:nth-of-type(9) {
        grid-area: halfFeed2;
      }
      &:nth-of-type(10) {
        grid-area: halfFeed3;
      }
      &:nth-of-type(11) {
        grid-area: halfFeed4;
      }
    }

    .fullFeedCard {
      grid-area: fullFeed1;
    }
  }
}